.loginImg {
  background-image: url("../../Assets/Images/Sign\ Up.jpg");

  background-repeat: no-repeat;
  width: 100%;
  height: auto;

  background-size: cover;
}

.loginImg img {
  width: 100%;
  height: 100%;
}
.loginLogoImg {
  width: 150px;
  height: 100px;
  margin: 0 auto !important;
}
.loginLogoImg img {
  width: 100%;
  height: 100%;
}

.inputBox input {
  width: 280px;
  padding: 8px 15px;
  margin: 5px;
}
.loginText {
  width: 315px;
  margin: 0 auto !important;
  text-align: left;
}
.loginText h6 {
  font-size: 22px !important;
  font-weight: bold !important;
  color: #000 !important;
  padding: 10px 0 !important;
}
.loginText span {
  color: #2c3051 !important;
  padding: 10px 0 !important;
}
.input_width {
  width: 320px;
  margin: 0 auto !important;
}
.loginButton {
  padding-top: 20px;
}
.LoginSec {
  display: inline-flex;
  justify-content: center;
}
.rightAlign {
  height: 70vh;
  margin: auto 0;
}
input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
  -webkit-transition: "color 9999s ease-out, background-color 9999s ease-out";
  -webkit-transition-delay: 9999s;
}

.googleBtn {
  background-color: #fff !important;
  color: #000 !important;
  width: 318px;
}
.googleGrid a {
  text-decoration: none;
}

.googleGrid img {
  height: 25px;
}
